import React from 'react';
import RealtimeSearch from "./RealtimeSearch";

let TITLE = 'CHUSEOK';
let AVATAR = {
    head : "발할라 패키지",
    body : ["발할라 칭호 상자", "발할라 오라 상자", "발할라 보너스 아바타 상자", "발할라 세라 상자", "무기 클론 아바타 상자"],
    tail : "발할라 아바타 풀세트 상자"
};

class Chuseok extends React.Component {

    render() {
        return (
            <RealtimeSearch
                TITLE={TITLE}
                AVATAR={AVATAR}
            />
        )
    }
}

export default Chuseok;