import React from 'react';
import RealtimeSearch from "./RealtimeSearch";

let TITLE = 'ARADSCHOOL';
let AVATAR = {
    head : "왕립 아라드 고교 리턴즈 패키지",
    body : ["왕립 아라드 고교 리턴즈 크리쳐 상자", "왕립 아라드 고교 리턴즈 세라 상자"],
    tail : "왕립 아라드 고교 리턴즈 아바타 풀세트 상자"
};

class Aradschool extends React.Component {

    render() {
        return (
            <RealtimeSearch
                TITLE={TITLE}
                AVATAR={AVATAR}
            />
        )
    }
}

export default Aradschool;